import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { TableData } from 'components/common';
import { Card, Row, Popconfirm, Col, message } from 'antd';
import { getPointLadderFours, deletePointLadderFourById } from 'services/pointLadderFour';

class PointLadderFourPage extends Component {
  state = { reload: 0 };

  columns = [
    {
      title: 'Xếp loại',
      dataIndex: 'label',
      key: 'label',
    },
    {
      title: 'Tên tiếng anh',
      dataIndex: 'labelInEnglish',
      key: 'labelInEnglish',
    },
    {
      title: 'Từ điểm',
      dataIndex: 'fromGrade',
      key: 'fromGrade',
    },
    {
      title: 'Đến điểm',
      dataIndex: 'toGrade',
      key: 'toGrade',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: (text, record) => (
        <Fragment>
          <BtnEdit href={`#point-ladder-four/form/edit/${record.id}`} />
          <Popconfirm
            title="Bạn có muốn xóa?"
            okText="Xóa"
            cancelText="Hủy"
            onConfirm={() => this.handleDelete(record.id)}
          >
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ),
    },
  ];

  handleDelete = async id => {
    try {
      await deletePointLadderFourById(id);
      message.success('Xóa thành công');
      this.setState({ reload: (this.state.reload += 1) });
    } catch (err) {
      message.error('Xóa không thành công');
    }
  };

  render() {
    const { reload } = this.state;

    return (
      <Card title="Xếp loại theo thang điểm 4">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/point-ladder-four/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              bordered
              columns={this.columns}
              service={getPointLadderFours}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <PointLadderFourPage />
  </Page>
);



